<template>
    <v-container fluid pa-0>
            <v-toolbar color="blue darken-3" dark>
                 <v-text-field dense class="pt-6" rounded outlined v-model="planCode" label="Plan Code"></v-text-field> 
                 <v-spacer></v-spacer>
                 <v-btn dark @click="loadData()" rounded outlined>Load Plan Code</v-btn>
            </v-toolbar>
                planData: {{planData}}
    </v-container>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            planData: {},
            planCode: ""
        }
    },
    mounted(){
        this.loadData()
    },
    methods: {
        loadData(){
            axios.get(`${this.baseUrl}plan/${this.planCode}`).then(res => {
                console.log(res.data)
                this.planData = res.data
            })
        }
    }
}
</script>